
























import { Data, PropType, SetupContext } from '@nuxtjs/composition-api'

export default {
  // props: { tagsList: Array, total: Number },
  props: {
    tagsList: {
      type: Array,
      default: [] as PropType<any>,
    },
    total: {
      type: Number,
      default: 0,
    },
  },
  setup(_props: Data, { emit }: SetupContext) {
    // 过滤得到标签文章
    const searchTag = (val: any) => {
      emit('searchTag', val)
    }
    return {
      searchTag,
    }
  },
}
